
import * as anchor from "@project-serum/anchor";
import "./index.css";
import { idMintMap } from "../../mintData";
import {
  HashLink as Link
} from "react-router-hash-link";
//Some of the sources:
//https://spl.solana.com/token#finding-all-token-accounts-for-a-specific-mint

export interface PondDirectoryProps {
  connection: anchor.web3.Connection;
  txTimeout: number;
  //mintAnchor: anchor.web3.PublicKey; //MintAnchor describes the mintHash that is used to detect the corresponding wallet
}


export const PondDirectory = (props: PondDirectoryProps) => {
  const ponds = Object.keys(idMintMap).map(id => 
    //<option value={key}>{tifs[key]}</option>
    <Link className="pondLink" to={"pond-"+id}><h1>Koin Koi #{id}</h1></Link> // renders <a href="/calendar/tomorrow">


);

  return(<div className="dirWrapper constructionHeadings">
     {ponds}
  </div>)
};



